.dropdown2{
    max-width: 304px;
    width: 100%;
    // height: 38px;
    padding: 6px 142px 6px 12px;
    font-size: 18px;
    line-height: 26px;
}
@media(max-width:576px){
    .dropdown2{
        max-width: 276px;
        padding: 6px 114px 6px 12px;
    }
}
.location2-content{
    display: flex;
    gap: 0 24px;
    margin-bottom: 80px;
    .location2-list{
        display: flex;
        width: calc((100% - 24px) / 2);
        .location2-card{
            margin-bottom: 48px;
            height: 100%;
        }
    }
    .location2-map{
        width: calc((100% - 24px) / 2);
        height: 100%;
    }
}

@media(max-width:992px){
    .location2-content{
        .location2-list{
            display: none;
        }
    }
    .location2-content{
        flex-direction: column;
        .location2-list-md{
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 24px;
            gap: 0 24px;

            .location2-card{
                width: calc((100% - 24px) / 2);
            }
            .location2-card-data{
                width: calc((100% - 24px) / 2);
            }
        }
        .location2-map{
            width: 100%;
            height: 100%;
        }
    }
}
@media(max-width:576px){
    .location2-content{
        padding: 0 13px;
        .location2-list-md{
            .location2-card{
                width: 100%;
            }
            .location2-card-data{
                width: 100%;
            }
        }
    }
}
@media(min-width:992px){
    .location2-content{
        .location2-list-md{
            display: none;
        }
    }
}

.location2-card-data{
    .location2-card-title{
        padding: 8px 24px;
        border: 1px solid #00000020;
        font-size: $fz-primary*1.5;
        line-height: $lh-primary*1.5;
    }
}